const {ccclass, property} = cc._decorator;

import DefaultBall from './DefaultBall'

const level: number[] = [2, 4, 8, 16, 32, 64, 128, 256, 512, 1024, 2048];

@ccclass
export default class Helloworld extends cc.Component {

    @property(cc.Node)
    gameBox: cc.Node = null;

    @property(cc.Prefab)
    defaultBall: cc.Prefab = null;

    @property(cc.Label)
    nextBallLabel: cc.Label = null;

    // 鱼塘 --- Start ---
    @property({type: cc.Node, displayName: "鱼塘Node"})
    water: cc.Node = null;

    @property({type: cc.Prefab, displayName: "鱼的Prefab"})
    fishPrefab: cc.Prefab = null;

    @property({type: cc.SpriteAtlas, displayName: "鱼的资源图片"})
    fishAtlas: cc.SpriteAtlas = null;
    // 鱼塘 --- End ---

    @property({type: [cc.AudioClip], displayName: "BGM"})
    bgmList: [cc.AudioClip] = [null];

    _touchDownState: boolean = false;
    _touchDownEvent: any = null;

    _nextBallNum: number = level[Math.ceil(Math.random()*level.length-1)];
    _ballIndex: number = 0;
    
    onLoad () {
        cc.director.getPhysicsManager().enabled = true;
        // cc.director.getPhysicsManager().debugDrawFlags = cc.PhysicsManager.DrawBits.e_aabbBit |
        //     cc.PhysicsManager.DrawBits.e_jointBit |
        //     cc.PhysicsManager.DrawBits.e_shapeBit;
        cc.director.getPhysicsManager().gravity = cc.v2(0, -640);
        this.nextBallLabel.string = this._nextBallNum + '';
    }

    start () {
        // 测试性能
        // this.gameBox.on(cc.Node.EventType.TOUCH_START, function(event){
        //     this._touchDownEvent = event;
        //     this._touchDownState = true;
        // }.bind(this));

        // this.gameBox.on(cc.Node.EventType.TOUCH_END, function(event){
        //     this._touchDownEvent = null;
        //     this._touchDownState = false;
        // }.bind(this));

        // 单次点击
        this.gameBox.on(cc.Node.EventType.TOUCH_START, function(event){
            this.makeDefaultBall(event)
        }.bind(this));

        this.generateFishes();
    }

    update() {
        if (this._touchDownState) {
            var point = this.gameBox.convertTouchToNodeSpaceAR(this._touchDownEvent);
            var ball = cc.instantiate(this.defaultBall);
            (ball.getComponent(DefaultBall) as DefaultBall).text = this._nextBallNum + '';
            (ball.getComponent(DefaultBall) as DefaultBall).index = this._ballIndex;
            this.gameBox.addChild(ball);
            ball.setPosition(point);

            // 生成下一个随机数
            this._nextBallNum = level[Math.ceil(Math.random()*level.length-1)];
            this._ballIndex += 1;
            this.nextBallLabel.string = this._nextBallNum + '';
        }
    }

    makeDefaultBall(event) {
        var point = this.gameBox.convertTouchToNodeSpaceAR(event);
            var ball = cc.instantiate(this.defaultBall);
            (ball.getComponent(DefaultBall) as DefaultBall).text = this._nextBallNum + '';
            (ball.getComponent(DefaultBall) as DefaultBall).index = this._ballIndex;
            this.gameBox.addChild(ball);
            ball.setPosition(point);

            // 生成下一个随机数
            this._nextBallNum = level[Math.ceil(Math.random()*level.length-1)];
            this._ballIndex += 1;
            this.nextBallLabel.string = this._nextBallNum + '';
    }

    // 生成鱼儿
    generateFishes() {
        this.fishAtlas.getSpriteFrames().forEach(function(spriteFrameItem) {
            let fish = cc.instantiate(this.fishPrefab);
            fish.getComponent(cc.Sprite).spriteFrame = spriteFrameItem;
            this.water.addChild(fish);
        }.bind(this));
    }
}
